*,
*:before,
*:after {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

.bg-color {
  background: linear-gradient(to bottom right, #007fc2, #00d6ee);
}

.visually-hidden {
  width: 1px;
  height: 1px;
  margin-top: -1px;
  margin-left: -1px;
  overflow: hidden;
  position: absolute;
  clip: rect(0, 0, 0, 0);
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

.header {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(100% - 20px);
  justify-content: center;
  font-family: sans-serif;
  font-weight: 700;
  color: #fff;
  font-size: 50px;
  padding-top: 100px;
  padding-bottom: 50px;
}

.rows {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.rows .card {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(33.333333333333336% - 20px);
  padding: 50px 30px 60px 30px;
  background: #fff;
  border-radius: 2%;
  margin-bottom: 20px;
  list-style-type: none;
}

.rows .card_photo {
  border-radius: 50%;
}

.rows .card_title {
  padding-bottom: 10px;
  padding-top: 20px;
  font-size: 35px;
  font-weight: 700;
}

.rows .card_feedback {
  font-size: 17px;
  padding-bottom: 25px;
}

.rows .card_date {
  display: block;
  margin-top: 10px;
  font-size: 20px;
  color: grey;
  font-style: italic;
}

.feature {
  background-color: #fff;
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
  margin-bottom: 20px;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(100% - 20px);
  margin-bottom: 45px;
}

.feature_icon {
  border-radius: 50%;
}

.feature_head {
  margin-left: 40%;
  margin-right: 40%;
  font-size: 30px;
  margin-bottom: 15px;
}

.feature_about {
  text-align: center;
  margin-bottom: 25px;
}

.feature_icon {
  padding-left: 30%;
}

.feature_text {
  text-align: center;
  padding-bottom: 15px;
}

.feature_rows {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
  width: 100%;
}

.feature_rows .feature_card {
  margin-bottom: 20px;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(25% - 20px);
  margin-bottom: 25px;
  list-style-type: none;
}

@media screen and (max-width: 1100px) {
  .header {
    justify-content: center;
  }

  .rows {
    justify-content: flex-start;
  }

  .rows .card {
    width: calc(33.333333333333336% - 20px);
  }

  .feature_rows .feature_card {
    width: calc(25% - 20px);
  }
}

@media screen and (max-width: 960px) {
  .header {
    width: calc(100% - 20px);
  }

  .rows .card {
    width: calc(50% - 20px);
  }

  .feature {
    width: calc(100% - 20px);
  }

  .feature_rows .feature_card {
    width: calc(25% - 20px);
  }
}

@media screen and (max-width: 780px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }

  .header {
    width: calc(100% - 20px);
  }

  .rows .card {
    width: calc(100% - 20px);
  }

  .feature {
    width: calc(100% - 20px);
  }

  .feature_rows .feature_card {
    width: calc(25% - 20px);
  }
}

@media screen and (max-width: 560px) {
  .header {
    justify-content: center;
    flex-wrap: nowrap;
  }

  .header {
    width: calc(100% - 20px);
  }

  .rows {
    justify-content: flex-start;
  }

  .rows .card {
    width: calc(100% - 20px);
  }

  .feature {
    width: calc(100% - 20px);
  }

  .feature_rows .feature_card {
    width: calc(25% - 20px);
  }
}